<template>
  <div class="container">
    <el-form
      :rules="rules"
      class="form-conntainer"
      ref="form"
      :model="form"
      label-width="140px"
    >
      <el-form-item label="项目名称" prop="projectId">
        <el-select
          v-model="form.projectId"
          placeholder="请选择项目名称"
          style="width: 100%;"
          @change="handleChangeProject"
        >
          <el-option
            v-for="attend in projectList"
            :key="attend.value"
            :label="attend.title"
            :value="attend.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="项目阶段" prop="stageId">
        <el-select
          v-model="form.stageId"
          placeholder="请选择项目阶段"
          style="width: 100%;"
        >
          <el-option
            v-for="attend in participateStage"
            :key="attend.value"
            :label="attend.title"
            :value="attend.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="所在地" prop="attendType">
        <el-select
          v-model="form.attendType"
          placeholder="请选择所在地"
          style="width: 100%;"
        >
          <el-option
            v-for="attend in attendTypeList"
            :key="attend.value"
            :label="attend.title"
            :value="attend.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="报工日期" prop="reportDate">
        <el-date-picker
          placeholder="选择时间"
          v-model="form.reportDate"
          style="width: 100%;"
          format="yyyy-MM-dd"
          value-format="yyyy-MM-dd"
        ></el-date-picker>
      </el-form-item>
      <el-form-item label="报工类型" prop="reportType">
        <el-select
          v-model="form.reportType"
          placeholder="请选择活动区域"
          style="width: 100%;"
        >
          <el-option
            v-for="attend in reportTypeList"
            :key="attend.value"
            :label="attend.title"
            :value="attend.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="日志" prop="reportDetail">
        <el-input type="textarea" v-model="form.reportDetail"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button style="width: 100%;" type="primary" @click="onSubmit('form')"
          >年轻人要讲武德</el-button
        >
      </el-form-item>
    </el-form>
    <el-dialog
      v-if="dialogTableVisible"
      title="填写token"
      :close-on-click-modal="false"
      :visible.sync="dialogTableVisible"
    >
      <el-input type="textarea" v-model.trim="token"></el-input>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogTableVisible = false">取 消</el-button>
        <el-button type="primary" @click="handleInputToken">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
const baseUrl = "https://hrm-api.1919.cn/hrm/ejr/api";

export default {
  data() {
    return {
      dialogTableVisible: true,
      token: "",
      form: {
        projectId: "",
        stageId: "",
        attendType: "COMPANY",
        reportDetail: "",
        reportDate: "",
        reportType: "WHOLE_DAY"
      },
      attendTypeList: [],
      reportTypeList: [],
      projectList: [],
      participateStage: [],
      rules: {
        projectId: [
          { required: true, message: "请选择项目名称", trigger: "blur" }
        ],
        stageId: [
          { required: true, message: "请选择项目阶段", trigger: "blur" }
        ],
        attendType: [
          { required: true, message: "请选择所在地", trigger: "blur" }
        ],
        reportDate: [
          { required: true, message: "请选择报工日期", trigger: "blur" }
        ],
        reportType: [
          { required: true, message: "请选择报工类型", trigger: "blur" }
        ],
        reportDetail: [
          { required: true, message: "请填写日志", trigger: "blur" }
        ]
      }
    };
  },
  mounted() {
    this.getAttendType();
    this.getReportType();
  },
  methods: {
    onSubmit(formData) {
      this.$refs[formData].validate(valid => {
        if (valid) {
          axios
            .post(baseUrl + "/dailyReport/add", this.form, {
              headers: {
                "x-rpc-wrap": 1,
                authorization: this.token
              }
            })
            .then(res => {})
            .catch(err => {
              this.$message.error("保存出错");
            });
        }
      });
    },

    handleInputToken() {
      this.dialogTableVisible = false;
      this.getProjectList();
    },
    handleChangeProject(projectIds) {
      this.getParticipateStage([projectIds]);
    },
    getAttendType() {
      axios.get(baseUrl + "/meta/dict/attend-type").then(res => {
        this.attendTypeList = res.data.data;
      });
    },
    getReportType() {
      axios.get(baseUrl + "/meta/dict/report-type").then(res => {
        this.reportTypeList = res.data.data;
      });
    },
    getProjectList() {
      axios
        .get(baseUrl + "/project/getParticipateProject", {
          headers: {
            "x-rpc-wrap": 1,
            authorization: this.token
          }
        })
        .then(res => {
          this.projectList = res.data.data;
        });
    },
    getParticipateStage(projectIds) {
      axios
        .post(
          baseUrl + "/stage/getParticipateStage",
          { projectIds },
          {
            headers: {
              "x-rpc-wrap": 1,
              authorization: this.token
            }
          }
        )
        .then(res => {
          this.participateStage = res.data.data;
        });
    }
  }
};
</script>
<style scoped>
.container {
  padding-top: 10vh;
}
.form-conntainer {
  width: 60%;
  margin: 0 auto;
}
</style>



